module.exports = [[module.id, "@charset \"UTF-8\";\r\n\r\n/*\r\n* Author: Vasterad\r\n* URL: http://purethemes.net\r\n*/\r\n\r\n/* ------------------------------------------------------------------- */\r\n\r\n/* Table of Contents\r\n----------------------------------------------------------------------\r\n\r\n\t01. Import Section ...................... Imported variables.\r\n\r\n\t02. Layout Components ................... Generic layout elements.\r\n\t\t# Topbar\r\n\t\t# Header\r\n\t\t# Sticky Header\r\n\t\t# Back to top\r\n\t\t# Navigation\r\n\t\t# mmenu.js - Mobile Menu\r\n\t\t# Search Forms Styling\r\n\t\t# Full Width Search Form\r\n\t\t# Parallax Background\r\n\t\t# Titlebar\r\n\t\t# Agents List\r\n\t\t# Listings Styles\r\n\t\t# Fullwidth Sections\r\n\t\t# Pagination\r\n\t\t# Property Page\r\n\t\t# Submit page\r\n\t\t# Compare Properties\r\n\t\t# My Account Page\r\n\t\t# Image Hovers\r\n\t\t# Contact Form\r\n\t\t# Blog Styles\r\n\r\n\t03. Script Styles ....................... jQuery add-ons CSS.\r\n\t\t# Google Maps\r\n\t\t# Owl Carousel\r\n\t\t# Slick Carousel\r\n\t\t# Magnific Popup\r\n\t\t# Dropzone\r\n\t\t# Chosen Plugin\r\n\t\t# Input with dropdown\r\n\r\n\t04. Shortcodes .......................... Template elements.\r\n\t\t# Buttons\r\n\t\t# Pricing Tables\r\n\t\t# Accordion / Toggles\r\n\t\t# Tabs\r\n\t\t# Tables\r\n\t\t# Notification Boxes\r\n\t\t# Social Icons\r\n\t\t# Info Box\r\n\t\t# Testimonials\r\n\t\t# Icon Boxes\r\n\t\t# Info Banner\r\n\t\t# Counters\r\n\r\n\t05. Others .............................. CSS helper classes etc.\r\n\t\t# Common Styles\r\n\t\t# Login Register\r\n\t\t# Offsets\r\n\r\n\t06. Media Queries ....................... Mobile style sheets.\r\n\r\n*/\r\n\r\n/* ------------------------------------------------------------------- */\r\n\r\n/* 01. Import Section\r\n---------------------------------------------------------------------- */\r\n\r\n/*@import url(\"bootstrap.css\"); /* Default Reset, Typography, Forms, Responsive Grid etc. */\r\n\r\n*/\r\n/*@import url(\"icons.css\"); /* Webfont Icons: FontAwesome, Line, Simple Line */\r\n*/\r\n/*@import url(\"http://fonts.googleapis.com/css?family=Varela+Round\");*/\r\n ------------------------------------------------------------------- \r\n/*  02. Layout Components\r\n---------------------------------------------------------------------- */\r\n/* ---------------------------------- */\r\n/* Topbar\r\n------------------------------------- */\r\n/* Common Styles */\r\n#top-bar {\r\n    background-color: #0e3a6b;\r\n    color: #fff;\r\n    padding: 0;\r\n    position: relative;\r\n    z-index: 1001;\r\n}\r\n\r\nul.top-bar-menu {\r\n    margin: 0;\r\n    padding: 8px 0;\r\n    -webkit-transform: translate3d(0,1px,0);\r\n    -moz-transform: translate3d(0,0,0);\r\n}\r\n\r\nul.top-bar-menu li {\r\n        font-size: 13px;\r\n        display: inline-block;\r\n        margin: 0 20px 0 0;\r\n    }\r\n\r\nul.top-bar-menu li a {\r\n            color: #fff;\r\n        }\r\n\r\nul.top-bar-menu li i {\r\n            color: #fff;\r\n            margin: 0 5px 0 0;\r\n            font-size: 15px;\r\n            position: relative;\r\n            top: 1px;\r\n        }\r\n\r\n#top-bar ul.social-icons {\r\n    position: relative;\r\n    z-index: 990;\r\n}\r\n\r\n/* Light Version */\r\n\r\n#top-bar.light {\r\n    background-color: #f5f5f5;\r\n    color: #888;\r\n}\r\n\r\n#top-bar.light a {\r\n        color: #888;\r\n    }\r\n\r\n#top-bar.light ul.top-bar-menu li i,\r\n    #top-bar.light .top-bar-dropdown span:after {\r\n        color: #a0a0a0;\r\n    }\r\n\r\n/* Topbar Dropdown\r\n------------------------------------- */\r\n\r\n.top-bar-dropdown {\r\n    cursor: pointer;\r\n    display: inline-block;\r\n}\r\n\r\n.top-bar-dropdown .options {\r\n        opacity: 0;\r\n        visibility: hidden;\r\n        position: absolute;\r\n        margin: 20px 0 0 0;\r\n        z-index: 1101;\r\n        -webkit-transition: opacity 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;\r\n        transition: opacity 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;\r\n        transition: opacity 0.25s ease-in-out, transform 0.25s ease-in-out;\r\n        transition: opacity 0.25s ease-in-out, transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;\r\n        -webkit-transform: translate3d(0,15px,0);\r\n                transform: translate3d(0,15px,0);\r\n        border-radius: 3px;\r\n    }\r\n\r\n.top-bar-dropdown.active .options {\r\n        opacity: 1;\r\n        pointer-events: auto;\r\n        visibility: visible;\r\n        -webkit-transform: translate3d(0,0,0);\r\n                transform: translate3d(0,0,0);\r\n    }\r\n\r\n.top-bar-dropdown .arrow {\r\n        width: 0;\r\n        height: 0;\r\n        border-left: 6px solid transparent;\r\n        border-right: 6px solid transparent;\r\n        border-bottom: 6px solid #262626;\r\n        position: absolute;\r\n        top: -5px;\r\n        left: 10px;\r\n    }\r\n\r\n.top-bar-dropdown span:after {\r\n        content: \"\\f0d7\";\r\n        font-family: \"FontAwesome\";\r\n        font-size: 14px;\r\n        color: #fff;\r\n        top: 1px;\r\n        margin-left: 6px;\r\n        position: relative;\r\n    }\r\n\r\n.top-bar-dropdown ul {\r\n        background: #262626;\r\n        max-width: 180px;\r\n        padding: 14px 0;\r\n    }\r\n\r\n.top-bar-dropdown ul li {\r\n            width: 100%;\r\n            display: block;\r\n            padding: 0;\r\n            line-height: 21px;\r\n        }\r\n\r\n.top-bar-dropdown ul li a {\r\n                padding: 5px 22px;\r\n                display: block;\r\n                color: #ccc;\r\n                z-index: 10;\r\n                position: relative;\r\n                -webkit-transition: color 0.25s;\r\n                transition: color 0.25s;\r\n                line-height: 21px;\r\n            }\r\n\r\n.top-bar-dropdown ul li a:hover {\r\n                    color: #fff;\r\n                    z-index: 1;\r\n                    position: relative;\r\n                }\r\n\r\n/* ---------------------------------- */\r\n\r\n/* Header\r\n------------------------------------- */\r\n\r\n#logo {\r\n    display: inline-block;\r\n    margin-top: 1px;\r\n}\r\n\r\n#logo img {\r\n        max-height: 43px;\r\n        width: auto;\r\n        -webkit-transform: translate3d(0,0,0);\r\n                transform: translate3d(0,0,0);\r\n    }\r\n\r\nimg.footer-logo {\r\n    max-height: 43px;\r\n    width: auto;\r\n    -webkit-transform: translate3d(0,0,0);\r\n            transform: translate3d(0,0,0);\r\n}\r\n\r\n#header {\r\n    position: relative;\r\n    z-index: 999;\r\n    padding: 13px 0 13px 0;\r\n    box-shadow: 0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);\r\n    background: #fff;\r\n}\r\n\r\n/* Header Widget */\r\n\r\n.header-widget {\r\n    position: relative;\r\n    top: -1px;\r\n    height: 54px;\r\n}\r\n\r\n.header-widget .button,\r\n    .header-widget .button.border {\r\n        margin-right: 0;\r\n    }\r\n\r\n.sign-in {\r\n    position: relative;\r\n    top: 14px;\r\n    color: #333;\r\n    margin-right: 40px;\r\n    vertical-align: top;\r\n    font-size: 15px;\r\n    font-weight: 600;\r\n}\r\n\r\n.header-widget .sign-in i {\r\n    padding-right: 3px;\r\n    vertical-align: top;\r\n    padding-top: 1px;\r\n}\r\n\r\n.sign-in i.fa.fa-user:before {\r\n    font-family: \"simple-line-icons\";\r\n    content: \"\\e066\";\r\n    font-size: 17px;\r\n    position: relative;\r\n    top: -1px;\r\n}\r\n\r\n.header-widget .sign-in:hover {\r\n    color: #66676b;\r\n}\r\n\r\n/* Full Width Header */\r\n\r\nheader.fullwidth .container {\r\n    width: 100%;\r\n    margin: 0;\r\n    padding: 0 40px;\r\n}\r\n\r\n.left-side {\r\n    float: left;\r\n    width: 65%;\r\n    display: inline-block;\r\n}\r\n\r\n.right-side {\r\n    float: right;\r\n    width: 35%;\r\n    display: inline-block;\r\n}\r\n\r\n#logo,\r\n#navigation {\r\n    float: left;\r\n}\r\n\r\n#logo {\r\n    margin-right: 40px;\r\n}\r\n\r\n.header-widget {\r\n    text-align: right;\r\n}\r\n\r\n/* Header Style 2\r\n------------------------------------- */\r\n\r\n#header-container.top-border {\r\n    border-top: 4px solid #66676b;\r\n}\r\n\r\n#header-container.header-style-2 .right-side {\r\n    width: 70%;\r\n}\r\n\r\n#header-container.header-style-2 .left-side {\r\n    width: 30%;\r\n}\r\n\r\n#header-container.header-style-2 #header {\r\n    padding-top: 0px;\r\n}\r\n\r\n#header-container.header-style-2 #header {\r\n    padding-bottom: 0;\r\n}\r\n\r\n/* Header Widget */\r\n\r\n.header-style-2 .header-widget {\r\n    margin-top: 8px;\r\n    margin-bottom: 8px;\r\n    float: right;\r\n    padding-left: 0;\r\n    text-align: left;\r\n}\r\n\r\n.header-style-2 .header-widget li {\r\n        list-style: none;\r\n        display: inline-block;\r\n        vertical-align: middle;\r\n        margin-right: 20px;\r\n        padding-right: 35px;\r\n        line-height: 24px;\r\n        position: relative;\r\n    }\r\n\r\n.header-style-2 .header-widget li:after {\r\n            position: absolute;\r\n            height: 48px;\r\n            content: \"\";\r\n            display: inline-block;\r\n            width: 1px;\r\n            background-color: #e0e0e0;\r\n            -webkit-transform: translateY(-50%);\r\n            transform: translateY(-50%);\r\n            top: 50%;\r\n            right: 0;\r\n        }\r\n\r\n.header-style-2 .header-widget li i {\r\n            float: left;\r\n            display: inline-block;\r\n            color: #66676b;\r\n            font-size: 34px;\r\n            width: 35px;\r\n            position: relative;\r\n            margin-top: 8px;\r\n            margin-right: 18px;\r\n            text-align: center;\r\n            -webkit-text-stroke: 0.5px #fff;\r\n        }\r\n\r\n.header-style-2 .header-widget li .widget-content {\r\n            float: left;\r\n            max-width: 150px;\r\n        }\r\n\r\n.header-style-2 .header-widget .sl-icon-location {\r\n        font-size: 36px;\r\n        margin: 5px 10px 0 8px;\r\n    }\r\n\r\n.header-style-2 .header-widget li span.title {\r\n        display: block;\r\n        color: #333;\r\n    }\r\n\r\n.header-style-2 .header-widget li span.data {\r\n        color: #808080;\r\n    }\r\n\r\n.header-style-2 .header-widget li.with-btn:after {\r\n        display: none;\r\n    }\r\n\r\n.header-style-2 .header-widget a.button {\r\n        margin-left: 13px;\r\n        line-height: 25px;\r\n        position: relative;\r\n        top: 3px;\r\n    }\r\n\r\n.header-style-2 .header-widget li:last-child {\r\n        margin-right: 0;\r\n        padding-right: 0;\r\n    }\r\n\r\n/* Header Style 2 - Right Side Menu Items */\r\n\r\n@media (min-width: 992px) {\r\n    .header-style-2 .right-side-menu-item {\r\n        float: right !important\r\n    }\r\n\r\n    .header-style-2 #navigation ul#responsive {\r\n        width: 100%;\r\n    }\r\n\r\n    .header-style-2 .sign-in i {\r\n        top: 3px;\r\n        position: relative;\r\n        padding-right: 5px;\r\n    }\r\n\r\n    .header-style-2 .user-menu {\r\n        float: right;\r\n        margin-top: -28px;\r\n        margin-right: 1px;\r\n    }\r\n\r\n        .header-style-2 .user-menu.active .user-name,\r\n        .header-style-2 .user-menu .user-name,\r\n        .header-style-2 .user-menu.active .user-name:hover,\r\n        .header-style-2 .user-menu .user-name:hover {\r\n            color: #fff !important;\r\n        }\r\n\r\n            .header-style-2 .user-menu.active .user-name:hover:after,\r\n            .header-style-2 .user-menu .user-name:hover:after,\r\n            .header-style-2 .user-menu.active .user-name:after,\r\n            .header-style-2 .user-menu .user-name:after {\r\n                color: #fff !important;\r\n            }\r\n\r\n        .header-style-2 .user-menu ul {\r\n            top: 50px;\r\n        }\r\n}\r\n\r\n@media (max-width: 992px) {\r\n    .header-style-2 .user-menu-container {\r\n        height: 42px;\r\n        display: block;\r\n        margin: 15px;\r\n    }\r\n\r\n    .mm-menu .sign-in i.fa.fa-user:before {\r\n        font-size: 15px;\r\n        position: relative;\r\n        top: 1px;\r\n        padding-right: 6px;\r\n    }\r\n}\r\n\r\n@media (max-width: 768px) {\r\n    .header-style-2 .right-side {\r\n        margin-top: 5px;\r\n    }\r\n\r\n    .header-style-2 .header-widget a.button.border {\r\n        margin-bottom: 0px;\r\n    }\r\n\r\n    #header-container.header-style-2 .header-widget {\r\n        zoom: 0.7;\r\n    }\r\n}\r\n\r\n/* ---------------------------------- */\r\n\r\n/* Sticky Header\r\n------------------------------------- */\r\n\r\n#navigation.style-2.cloned,\r\n#header.cloned {\r\n    position: fixed;\r\n    top: 0px;\r\n    width: 100%;\r\n    z-index: 999;\r\n}\r\n\r\n#header.cloned {\r\n    background-color: #fff;\r\n}\r\n\r\n#navigation.style-2.cloned {\r\n    margin-top: 0;\r\n    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.12);\r\n}\r\n\r\n/* Sticky header styles */\r\n\r\n#header.cloned #logo img {\r\n    -webkit-transform: translate3d(0,0,0);\r\n            transform: translate3d(0,0,0);\r\n    max-width: 120px;\r\n    margin-top: 1px;\r\n}\r\n\r\n#header.cloned {\r\n    padding: 18px 0 7px 0;\r\n}\r\n\r\n.sticky-logo {\r\n    display: none;\r\n}\r\n\r\n#navigation.style-2.cloned .sticky-logo {\r\n    display: inline-block;\r\n    float: left;\r\n    padding: 0;\r\n    margin-right: 30px;\r\n    margin-top: 12px;\r\n}\r\n\r\n#navigation.style-2.cloned .sticky-logo img {\r\n        max-height: 32px;\r\n        width: auto;\r\n        -webkit-transform: translate3d(0,0,0);\r\n                transform: translate3d(0,0,0);\r\n    }\r\n\r\n#navigation.style-2.cloned.unsticky,\r\n#header.cloned.unsticky {\r\n    opacity: 1;\r\n    visibility: hidden;\r\n    pointer-events: none;\r\n    -webkit-transform: translate(0,-100%) scale(1);\r\n            transform: translate(0,-100%) scale(1);\r\n    -webkit-transition: 0.3s;\r\n    transition: 0.3s;\r\n}\r\n\r\n#navigation.style-2.cloned.unsticky {\r\n    opacity: 0;\r\n}\r\n\r\n#navigation.style-2.cloned,\r\n#header.cloned.sticky {\r\n    opacity: 1;\r\n    visibility: visible;\r\n    -webkit-transform: translate(0,0) scale(1);\r\n            transform: translate(0,0) scale(1);\r\n    -webkit-transition: 0.3s;\r\n    transition: 0.3s;\r\n}\r\n\r\n/*----------------------------------- */\r\n\r\n/* Footer\r\n------------------------------------- */\r\n\r\n#footer {\r\n    background-color: #fcfcfc;\r\n    box-shadow: inset 0 20px 50px -20px rgba(0,0,0,0.06);\r\n}\r\n\r\n#footer.sticky-footer {\r\n        box-shadow: none;\r\n    }\r\n\r\n#footer h4 {\r\n        color: #333;\r\n        font-size: 21px;\r\n        margin-bottom: 25px;\r\n    }\r\n\r\n.footer-shadow {\r\n    width: 100%;\r\n    display: block;\r\n    background: #fff;\r\n    height: 200px;\r\n    margin-top: -200px;\r\n    position: relative;\r\n    z-index: -1;\r\n    box-shadow: 0 20px 40px -20px rgba(0,0,0,0.06);\r\n}\r\n\r\n.text-widget span {\r\n    color: #707070;\r\n}\r\n\r\n.text-widget span.address {\r\n        display: block;\r\n        margin-bottom: 10px;\r\n    }\r\n\r\n/*  Links  */\r\n\r\nul.footer-links {\r\n    margin: 0 0 15px 0;\r\n    padding: 0;\r\n    width: 50%;\r\n    box-sizing: border-box;\r\n    display: inline-block;\r\n    float: left;\r\n}\r\n\r\n.footer-links li {\r\n    position: relative;\r\n    list-style: none;\r\n}\r\n\r\n.footer-links li a {\r\n        padding: 0 0 9px 16px;\r\n        display: inline-block;\r\n        -webkit-transition: all 0.2s ease-in-out;\r\n        transition: all 0.2s ease-in-out;\r\n        color: #707070;\r\n        line-height: 21px;\r\n    }\r\n\r\n.footer-links li a:hover {\r\n            padding-left: 22px;\r\n            color: #66676b;\r\n        }\r\n\r\n.footer-links li:before {\r\n        font-family: \"FontAwesome\";\r\n        font-size: 20px;\r\n        content: \"\\f105\";\r\n        position: absolute;\r\n        left: 0;\r\n        top: -2px;\r\n        padding: 0 7px 0 0;\r\n        color: #ccc;\r\n    }\r\n\r\n/* Footer Bottom */\r\n\r\n.copyrights {\r\n    color: #707070;\r\n    border-top: 1px solid rgba(0,0,0,0.09);\r\n    text-align: center;\r\n    padding: 40px 0;\r\n    display: inline-block;\r\n    width: 100%;\r\n}\r\n\r\n#footer.alt .copyrights {\r\n    margin-top: 0;\r\n    border: none;\r\n    padding: 25px;\r\n}\r\n\r\n.copyrights a {\r\n    color: #707070;\r\n}\r\n\r\n/* Dark Styles */\r\n\r\n#footer.dark {\r\n    background: #222;\r\n    color: #aaa;\r\n}\r\n\r\n#footer.dark a,\r\n    #footer.dark h4,\r\n    #footer.dark a:hover {\r\n        color: #fff;\r\n    }\r\n\r\n#footer.dark .text-widget,\r\n    #footer.dark .text-widget span,\r\n    #footer.dark .footer-links li a {\r\n        color: #aaa;\r\n    }\r\n\r\n#footer.dark .footer-links li a:hover {\r\n            color: #fff;\r\n        }\r\n\r\n#footer.dark .footer-links li:before {\r\n        color: #666;\r\n    }\r\n\r\n#footer.dark .copyrights {\r\n        color: #aaa;\r\n        border-top: 1px solid rgba(255,255,255,0.09);\r\n    }\r\n\r\n#footer .social-icons li a {\r\n    opacity: 0.8;\r\n    -webkit-transition: opacity 0.35s;\r\n    transition: opacity 0.35s;\r\n}\r\n\r\n#footer .social-icons li a:hover {\r\n        opacity: 1;\r\n    }\r\n\r\n/* ---------------------------------- */\r\n\r\n/* Back to top\r\n------------------------------------- */\r\n\r\n#backtotop {\r\n    position: fixed;\r\n    right: 0;\r\n    display: none;\r\n    bottom: 25px;\r\n    margin: 0 25px 0 0;\r\n    z-index: 999;\r\n}\r\n\r\n#backtotop a {\r\n        text-decoration: none;\r\n        border: 0 none;\r\n        display: block;\r\n        width: 44px;\r\n        height: 44px;\r\n        background-color: #66676b;\r\n        opacity: 1;\r\n        -webkit-transition: all 0.3s;\r\n        transition: all 0.3s;\r\n        border-radius: 3px;\r\n        text-align: center;\r\n        font-size: 26px\r\n    }\r\n\r\nbody #backtotop a {\r\n    color: #fff;\r\n}\r\n\r\n#backtotop a:after {\r\n    content: \"\\f106\";\r\n    font-family: \"FontAwesome\";\r\n    position: relative;\r\n    display: block;\r\n    top: 50%;\r\n    -webkit-transform: translateY(-55%);\r\n    transform: translateY(-55%);\r\n}\r\n\r\n/*----------------------------------- */\r\n\r\n/* Mortagege Calculator\r\n------------------------------------- */\r\n\r\nbutton {\r\n    outline: none;\r\n}\r\n\r\n.mortgageCalc input,\r\n.mortgageCalc input:focus {\r\n    border-radius: 3px;\r\n    padding-left: 65px;\r\n    margin-bottom: 0;\r\n    background-color: #fdfdfd;\r\n    border: 1px solid #e0e0e0;\r\n    -webkit-transition: border 0.3s, box-shadow 0.3s, background-color 0.3s;\r\n    transition: border 0.3s, box-shadow 0.3s, background-color 0.3s;\r\n}\r\n\r\n.mortgageCalc input:focus {\r\n        box-shadow: 0 0 12px rgba(0,0,0,0.05);\r\n    }\r\n\r\n.mortgageCalc .calc-input {\r\n    position: relative;\r\n    height: 51px;\r\n    display: block;\r\n    margin-bottom: 15px;\r\n}\r\n\r\n.mortgageCalc .fa {\r\n    background: -webkit-gradient(linear, left top, left bottom, from(#f8f8f8), to(#f0f0f0));\r\n    background: linear-gradient(to bottom, #f8f8f8 0%, #f0f0f0);\r\n    color: #999;\r\n    border-radius: 3px 0 0 3px;\r\n    top: 1px;\r\n    left: 1px;\r\n    position: absolute;\r\n    font-weight: normal;\r\n    font-size: 14px;\r\n    text-align: center;\r\n    padding: 0;\r\n    margin: 0;\r\n    width: 49px;\r\n    line-height: 49px;\r\n    height: 49px;\r\n    border-right: 1px solid #e0e0e0;\r\n}\r\n\r\n.mortgageCalc button.button {\r\n    margin: 8px 0 10px 0;\r\n    display: inline-block;\r\n}\r\n\r\n.mortgageCalc .calc-output-container {\r\n    opacity: 0;\r\n    width: 100%;\r\n    max-height: 0;\r\n    overflow: hidden;\r\n    margin-bottom: 0;\r\n    padding: 0;\r\n    -webkit-transition: opacity 0.3s, max-height 0.7s;\r\n    transition: opacity 0.3s, max-height 0.7s;\