* ---------------------------------- */\r\n\r\n/* Agents List\r\n------------------------------------- */\r\n\r\n.agents-container {\r\n    position: relative;\r\n    z-index: 9;\r\n}\r\n\r\n.agent {\r\n    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);\r\n    background-color: #fff;\r\n    display: block;\r\n    border-radius: 3px;\r\n    margin-bottom: 30px;\r\n}\r\n\r\n/* Agent Avatar */\r\n\r\n.agent-avatar {\r\n    width: 100%;\r\n}\r\n\r\n.agent-avatar img {\r\n        border-radius: 3px 3px 0 0;\r\n        width: 100%;\r\n        height: 100%;\r\n        -webkit-transform: translate3d(0);\r\n                transform: translate3d(0);\r\n    }\r\n\r\n.agent-avatar a {\r\n        position: relative;\r\n        width: 100%;\r\n        height: 100%;\r\n        display: block;\r\n        overflow: hidden;\r\n    }\r\n\r\n.agent-avatar a:before {\r\n            position: absolute;\r\n            height: 100%;\r\n            width: 100%;\r\n            display: block;\r\n            top: 0;\r\n            left: 0;\r\n            content: \"\";\r\n            z-index: 9;\r\n            opacity: 0;\r\n            background: #66676b;\r\n            background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, transparent), to(#66676b));\r\n            background: linear-gradient(to bottom, transparent 50%, #66676b);\r\n            -webkit-transition: opacity 0.4s;\r\n            transition: opacity 0.4s;\r\n        }\r\n\r\n.view-profile-btn {\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 50%;\r\n    z-index: 101;\r\n    -webkit-transform: translate(-50%, -80%);\r\n            transform: translate(-50%, -80%);\r\n    opacity: 0;\r\n    -webkit-transition: 0.35s;\r\n    transition: 0.35s;\r\n    text-align: center;\r\n    display: inline-block;\r\n    margin: 0 auto;\r\n    color: #fff;\r\n    background-color: #66676b;\r\n    padding: 7px 14px;\r\n    border-radius: 3px;\r\n}\r\n\r\n.agent-avatar a:hover .view-profile-btn {\r\n    opacity: 1;\r\n    -webkit-transform: translate(-50%,-50.5%);\r\n            transform: translate(-50%,-50.5%);\r\n}\r\n\r\n.agent-avatar a:hover:before {\r\n    opacity: 0.9;\r\n}\r\n\r\n/* Agent details list */\r\n\r\n.agent-contact-details {\r\n    list-style: none;\r\n    padding: 0;\r\n    margin: 0;\r\n    color: #909090;\r\n    display: block;\r\n    padding: 18px 32px;\r\n    background-color: #f7f7f7;\r\n}\r\n\r\n.agent-contact-details li {\r\n        position: relative;\r\n        margin-left: 28px;\r\n        line-height: 24px;\r\n        padding: 3px 0;\r\n        word-break: break-all;\r\n    }\r\n\r\n.agent-contact-details li a {\r\n            color: #909090;\r\n        }\r\n\r\n.agent-contact-details li a:hover {\r\n                color: #66676b;\r\n            }\r\n\r\n.agent-contact-details li i {\r\n            font-size: 18px;\r\n            position: relative;\r\n            top: 5px;\r\n            left: -28px;\r\n            position: absolute;\r\n        }\r\n\r\n.agent-contact-details li i.fa {\r\n                font-size: 17px;\r\n            }\r\n\r\n/* Agent socials */\r\n\r\nbody .agent ul.social-icons {\r\n    margin-left: 33px;\r\n}\r\n\r\nbody .agent ul.social-icons li {\r\n        margin: 11px 0 6px 0;\r\n    }\r\n\r\nbody .agent .social-icons li a {\r\n    -webkit-transform: scale(0.92);\r\n            transform: scale(0.92);\r\n    width: 34px;\r\n}\r\n\r\nbody .agent .social-icons li a.facebook {\r\n        width: 30px;\r\n        position: relative;\r\n        top: 2px;\r\n    }\r\n\r\nbody .agent .social-icons li a::before {\r\n        color: #b0b0b0;\r\n    }\r\n\r\n/* Agent Content*/\r\n\r\n.agent-name {\r\n    padding: 24px 32px;\r\n}\r\n\r\n.agent-name h4 {\r\n        margin: 0;\r\n        font-size: 19px;\r\n    }\r\n\r\n.agent-name span {\r\n        font-size: 16px;\r\n        color: #909090;\r\n        margin: 4px 0 -2px 0;\r\n        line-height: 24px;\r\n        display: block;\r\n    }\r\n\r\n/* Agents Page\r\n------------------------------------- */\r\n\r\n.agent-page {\r\n    padding: 0;\r\n    display: -webkit-box;\r\n    display: flex;\r\n    padding-right: 30px;\r\n}\r\n\r\n.agent.agent-page {\r\n    margin-bottom: 50px;\r\n}\r\n\r\n.agent-page .agent-content,\r\n.agent-page .agent-avatar,\r\n.agent-page .agent-form {\r\n    -webkit-box-flex: 3;\r\n            flex: 3;\r\n}\r\n\r\n.agent-page .agent-avatar {\r\n    -webkit-box-flex: 1.5;\r\n            flex: 1.5;\r\n    margin: 0;\r\n}\r\n\r\n.agent-page .agent-content {\r\n    padding: 0 40px;\r\n    padding-top: 30px;\r\n    padding-bottom: 19px;\r\n}\r\n\r\n.agent-page .agent-avatar img {\r\n    border-radius: 3px 0 0 3px;\r\n    height: auto;\r\n}\r\n\r\n.agent-page .agent-contact-details {\r\n    border-radius: 3px;\r\n    background: -webkit-gradient(linear, right top, left top, from(#fff), to(#f7f7f7));\r\n    background: linear-gradient(to left, #fff 0%, #f7f7f7);\r\n}\r\n\r\n.agent-page .agent-name {\r\n    padding-left: 0;\r\n    padding-top: 10px;\r\n}\r\n\r\nbody .agent-page ul.social-icons {\r\n    margin-left: 0;\r\n}\r\n\r\n.agent-page .agent-name h4 {\r\n    font-size: 21px;\r\n    color: #66676b;\r\n}\r\n\r\n.agent-page .agent-name span {\r\n    font-size: 17px;\r\n    margin-top: 5px;\r\n}\r\n\r\n/* Agent Page long content*/\r\n\r\n.agent-page.long-content .agent-avatar {\r\n    margin: 0 26px 0 0;\r\n}\r\n\r\n.agent-page.long-content .agent-avatar img {\r\n        margin: 32px 0 32px 32px;\r\n        border-radius: 3px;\r\n    }\r\n\r\n/* ---------------------------------- */\r\n\r\n/* Agency List\r\n------------------------------------- */\r\n\r\n.agent.agency {\r\n    margin-bottom: 30px;\r\n}\r\n\r\n.agent.agency .agent-avatar {\r\n        -webkit-box-flex: 1;\r\n                flex: 1;\r\n    }\r\n\r\nbody .agent.agency .social-icons li a {\r\n    border: 1px solid #e0e0e0;\r\n    height: 45px;\r\n    width: 45px;\r\n    border-radius: 4px;\r\n    top: 0 !important;\r\n}\r\n\r\n.agent.agency .social-icons {\r\n    left: 0;\r\n    margin: 7px 0 0px 0;\r\n}\r\n\r\n.agent.agency .agent-content {\r\n    padding-bottom: 25px;\r\n}\r\n\r\n.agent.agency .agent-name h4,\r\n.agent.agency .agent-name h4 a {\r\n    color: #333;\r\n}\r\n\r\n.agent.agency .agent-name span {\r\n    margin-top: 5px;\r\n    font-size: 17px;\r\n}\r\n\r\n.agent.agency .agent-name span i {\r\n        margin-right: 2px;\r\n    }\r\n\r\n.agency-map #propertyMap {\r\n    max-height: 300px;\r\n    height: 300px;\r\n}\r\n\r\n.agency-tabs .tabs-container {\r\n    overflow: visible;\r\n}\r\n\r\n.agency-tabs.style-1 .tabs-container .tab-content {\r\n    padding-top: 45px;\r\n}\r\n\r\n/*----------------------------------- */\r\n\r\n/* Fullscreen Map Section\r\n------------------------------------- */\r\n\r\n.fs-container {\r\n    height: 100%;\r\n    width: 100%;\r\n}\r\n\r\n.fs-container #map-container {\r\n        position: relative;\r\n        top: 0;\r\n        left: 0;\r\n        height: 100%;\r\n        width: 100%;\r\n        z-index: 990;\r\n        margin-bottom: 0px;\r\n    }\r\n\r\n.fs-content {\r\n    max-height: 100%;\r\n    overflow: auto;\r\n    padding: 0;\r\n}\r\n\r\n.fs-container section.search {\r\n    padding: 32px 40px 47px 40px;\r\n}\r\n\r\n.fs-container .fs-listings {\r\n    padding: 0 25px;\r\n    margin: 0;\r\n}\r\n\r\n.fs-switcher {\r\n    padding: 0 25px;\r\n    margin: 0;\r\n}\r\n\r\n/* Inner Containers */\r\n\r\n.fs-inner-container {\r\n    width: 50%;\r\n    height: 100%;\r\n    float: left;\r\n    display: inline-block;\r\n}\r\n\r\n.fs-inner-container {\r\n    width: 55%;\r\n}\r\n\r\n.fs-inner-container:first-child {\r\n        width: 45%;\r\n    }\r\n\r\nbutton.fs-map-btn.button {\r\n    position: relative;\r\n    float: right;\r\n    top: 6px;\r\n    padding: 10px 40px;\r\n    margin: 0;\r\n}\r\n\r\n.fs-container .more-search-options-trigger {\r\n    position: relative;\r\n    top: 10px;\r\n}\r\n\r\n.showing-results {\r\n    color: #888;\r\n    margin: 5px 0 25px 0;\r\n}\r\n\r\n.listings-container.fs-listings {\r\n    left: 0;\r\n    width: 100%;\r\n    position: relative;\r\n}\r\n\r\n.listings-container.fs-listings.grid-layout {\r\n        left: 0;\r\n        width: 100%;\r\n        position: relative;\r\n    }\r\n\r\n.listings-container.fs-listings.list-layout {\r\n        padding: 0 40px;\r\n    }\r\n\r\n.listings-container.fs-listings.list-layout .listing-content {\r\n            -webkit-box-flex: 4;\r\n                    flex: 4;\r\n        }\r\n\r\n.listings-container.fs-listings.list-layout .listing-img-container {\r\n            -webkit-box-flex: 0;\r\n                    flex: 0 auto;\r\n            max-width: 300px;\r\n        }\r\n\r\n/* Grid layout below 1366px screens */\r\n\r\n@media (max-width:1365px) {\r\n    .listings-container.fs-listings.grid-layout .listing-item {\r\n        width: 100%;\r\n        margin: 0 0 30px 0;\r\n    }\r\n\r\n    .listings-container.fs-listings.grid-layout {\r\n        padding: 0 40px;\r\n    }\r\n\r\n    .listings-container.fs-listings.list-layout .listing-item {\r\n        display: block;\r\n    }\r\n\r\n    .listings-container.fs-listings.list-layout .details.button.border {\r\n        display: none;\r\n    }\r\n\r\n    .listings-container.fs-listings.list-layout .listing-img-container {\r\n        max-width: 100%;\r\n    }\r\n\r\n    .fs-switcher .layout-switcher {\r\n        display: none;\r\n    }\r\n}\r\n\r\n/* Forms Grid */\r\n\r\n.col-fs-12,\r\n.col-fs-11,\r\n.col-fs-10,\r\n.col-fs-9,\r\n.col-fs-8,\r\n.col-fs-7,\r\n.col-fs-6,\r\n.col-fs-5,\r\n.col-fs-4,\r\n.col-fs-3,\r\n.col-fs-2,\r\n.col-fs-1 {\r\n    float: left;\r\n    padding: 0 10px;\r\n}\r\n\r\n.col-fs-12 {\r\n    width: 100%\r\n}\r\n\r\n.col-fs-11 {\r\n    width: 91.66666667%\r\n}\r\n\r\n.col-fs-10 {\r\n    width: 83.33333333%\r\n}\r\n\r\n.col-fs-9 {\r\n    width: 75%\r\n}\r\n\r\n.col-fs-8 {\r\n    width: 66.66666667%\r\n}\r\n\r\n.col-fs-7 {\r\n    width: 58.33333333%\r\n}\r\n\r\n.col-fs-6 {\r\n    width: 50%\r\n}\r\n\r\n.col-fs-5 {\r\n    width: 41.66666667%\r\n}\r\n\r\n.col-fs-4 {\r\n    width: 33.33333333%\r\n}\r\n\r\n.col-fs-3 {\r\n    width: 25%\r\n}\r\n\r\n.col-fs-2 {\r\n    width: 16.66666667%\r\n}\r\n\r\n.col-fs-1 {\r\n    width: 8.33333333%\r\n}\r\n\r\n@media (max-width:1599px) {\r\n    .col-fs-12,\r\n    .col-fs-11,\r\n    .col-fs-10,\r\n    .col-fs-9,\r\n    .col-fs-8,\r\n    .col-fs-7,\r\n    .col-fs-6 {\r\n        width: 100%\r\n    }\r\n\r\n    .col-fs-5,\r\n    .col-fs-4,\r\n    .col-fs-3,\r\n    .col-fs-2,\r\n    .col-fs-1 {\r\n        width: 50%\r\n    }\r\n}\r\n\r\n@media (max-width: 992px) {\r\n    .col-fs-12,\r\n    .col-fs-11,\r\n    .col-fs-10,\r\n    .col-fs-9,\r\n    .col-fs-8,\r\n    .col-fs-7,\r\n    .col-fs-6,\r\n    .col-fs-5,\r\n    .col-fs-4,\r\n    .col-fs-3,\r\n    .col-fs-2,\r\n    .col-fs-1 {\r\n        width: 100%\r\n    }\r\n\r\n    .overflow-hidden {\r\n        overflow: hidden;\r\n    }\r\n}\r\n\r\n/* ---------------------------------- */\r\n\r\n/* Listings Styles\r\n------------------------------------- */\r\n\r\n.fs-listings,\r\n.listings-container {\r\n    position: relative;\r\n    z-index: 9;\r\n}\r\n\r\n.listing-item {\r\n    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);\r\n    background-color: #fff;\r\n    display: block;\r\n    width: 100%;\r\n    border-radius: 3px;\r\n    margin-bottom: 30px;\r\n}\r\n\r\n.owl-carousel .listing-item {\r\n    margin-bottom: 0;\r\n}\r\n\r\n.listing-img-container {\r\n    position: relative;\r\n    height: 100%;\r\n    width: 100%;\r\n    display: block;\r\n}\r\n\r\n.listing-img-container img {\r\n        width: 100%;\r\n        border-radius: 3px 3px 0 0;\r\n    }\r\n\r\n.listing-img-container:after,\r\n    .listing-img-container:before {\r\n        position: absolute;\r\n        height: 100%;\r\n        width: 100%;\r\n        display: block;\r\n        top: 0;\r\n        left: 0;\r\n        content: \"\";\r\n        z-index: 9;\r\n    }\r\n\r\n.listing-img-container:before {\r\n        opacity: 0.9;\r\n        background: -webkit-gradient(linear, left top, left bottom, color-stop(65%, transparent), to(#2f2f2f));\r\n        background: linear-gradient(to bottom, transparent 65%, #2f2f2f);\r\n        -webkit-transition: opacity .3s;\r\n        transition: opacity .3s;\r\n    }\r\n\r\n.listing-img-container:after {\r\n        opacity: 0;\r\n        background: -webkit-gradient(linear, left top, left bottom, color-stop(60%, transparent), to(#66676b));\r\n        background: linear-gradient(to bottom, transparent 60%, #66676b);\r\n        -webkit-transition: opacity .4s;\r\n        transition: opacity .4s;\r\n    }\r\n\r\n.listing-img-container:hover:before {\r\n        opacity: 0;\r\n        -webkit-transition: opacity 0.4s;\r\n        transition: opacity 0.4s;\r\n    }\r\n\r\n.listing-img-container:hover:after {\r\n        opacity: 0.9;\r\n        -webkit-transition: opacity 0.3s;\r\n        transition: opacity 0.3s;\r\n    }\r\n\r\n/* Content on image */\r\n\r\n.listing-img-content {\r\n    position: absolute;\r\n    z-index: 99;\r\n    bottom: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    box-sizing: border-box;\r\n    padding: 25px 30px;\r\n}\r\n\r\n.listing-img-content span {\r\n        color: #fff;\r\n        font-size: 23px;\r\n    }\r\n\r\n.listing-img-content .listing-price i {\r\n        display: block;\r\n        font-style: normal;\r\n        font-size: 16px;\r\n        padding: 0;\r\n        margin: -4px 0 0 0;\r\n    }\r\n\r\n/* Badges */\r\n\r\n.listing-badges {\r\n    position: absolute;\r\n    top: 0;\r\n    z-index: 999;\r\n    right: 0;\r\n    width: 100%;\r\n    display: block;\r\n    font-size: 13px;\r\n    padding: 0px;\r\n    overflow: hidden;\r\n    height: 100px;\r\n}\r\n\r\n.listing-badges span {\r\n        color: #fff;\r\n        display: inline-block;\r\n        padding: 1px 10px;\r\n        float: right;\r\n        background-color: rgba(74,74,76,0.7);\r\n        border-radius: 3px;\r\n        top: 15px;\r\n        right: 15px;\r\n        position: absolute;\r\n    }\r\n\r\n.listing-badges .featured {\r\n        background-color: #66676b;\r\n        float: left;\r\n        -webkit-transform: rotate(-45deg);\r\n                transform: rotate(-45deg);\r\n        left: -67px;\r\n        top: 17px;\r\n        position: relative;\r\n        text-align: center;\r\n        width: 200px;\r\n        font-size: 12.5px;\r\n        margin: 0;\r\n    }\r\n\r\n/* Like Icon */\r\n\r\nspan.like-icon {\r\n    color: #fff;\r\n    font-size: 24px;\r\n    position: absolute;\r\n    z-index: 101;\r\n    right: 30px;\r\n    width: 23px;\r\n    height: 26px;\r\n    bottom: 28px;\r\n}\r\n\r\n.like-icon:before,\r\n.like-icon:after {\r\n    -webkit-transition: opacity 0.3s, -webkit-transform 0.4s cubic-bezier(.0,-.41,.19,2.5);\r\n    transition: opacity 0.3s, -webkit-transform 0.4s cubic-bezier(.0,-.41,.19,2.5);\r\n    transition: opacity 0.3s, transform 0.4s cubic-bezier(.0,-.41,.19,2.5);\r\n    transition: opacity 0.3s, transform 0.4s cubic-bezier(.0,-.41,.19,2.5), -webkit-transform 0.4s cubic-bezier(.0,-.41,.19,2.5);\r\n    font-family: \"FontAwesome\";\r\n    position: absolute;\r\n    right: 0;\r\n}\r\n\r\n.like-icon:before {\r\n    content: \"\\f006\";\r\n    -webkit-transform: scale(0.95);\r\n            transform: scale(0.95);\r\n}\r\n\r\n.like-icon:after {\r\n    content: \"\\f005\";\r\n    opacity: 0;\r\n    -webkit-transform: scale(0.5);\r\n            transform: scale(0.5);\r\n    color: #ffcf00;\r\n}\r\n\r\n.like-icon.liked:after {\r\n    opacity: 1;\r\n    -webkit-transform: scale(1.1);\r\n            transform: scale(1.1);\r\n}\r\n\r\n/* Listing Content */\r\n\r\n.listing-title {\r\n    padding: 15px 30px;\r\n    padding-top: 12px;\r\n}\r\n\r\n.listing-title h4 a {\r\n        font-size: 20px;\r\n        color: #333;\r\n        line-height: 31px;\r\n    }\r\n\r\n.listing-title h4 a:hover {\r\n            color: #66676b;\r\n        }\r\n\r\n.listing-address {\r\n    color: #909090;\r\n    top: -7px;\r\n    position: relative;\r\n}\r\n\r\n.list-layout .listing-address {\r\n    top: -5px;\r\n}\r\n\r\na.listing-address {\r\n    display: inline-block;\r\n    line-height: 24px;\r\n}\r\n\r\n.listing-address:hover {\r\n    color: #707070;\r\n}\r\n\r\n.listing-address i {\r\n    padding-right: 3px;\r\n    line-height: 0;\r\n}\r\n\r\n/* Listing Details */\r\n\r\nul.listing-details {\r\n    background-color: #f7f7f7;\r\n    list-style: none;\r\n    padding: 0;\r\n    margin: 0;\r\n    display: inline-block;\r\n    width: 100%;\r\n    padding: 18px 30px 18px 0;\r\n}\r\n\r\nul.listing-details li {\r\n        width: auto;\r\n        box-sizing: border-box;\r\n        margin: 0;\r\n        color: #868686;\r\n        padding: 3px 0 3px 30px;\r\n        display: inline-block;\r\n        line-height: 26px;\r\n    }\r\n\r\n/* Listing Footer */\r\n\r\n.listing-footer {\r\n    color: #909090;\r\n    padding: 15px 30px;\r\n}\r\n\r\n.listing-footer i {\r\n        padding-right: 3px;\r\n    }\r\n\r\n.listing-footer a {\r\n        color: #909090;\r\n    }\r\n\r\n.listing-footer a:hover {\r\n            color: #707070;\r\n        }\r\n\r\n.listing-footer span {\r\n        float: right;\r\n    }\r\n\r\n/* property main features */\r\n\r\n.listing-features {\r\n    background-color: #f7f7f7;\r\n    background: -webkit-gradient(linear, left top, left bottom, from(#f9f9f9), to(#f6f6f6));\r\n    background: linear-gradient(to bottom, #f9f9f9 0%, #f6f6f6);\r\n    width: 100%;\r\n    display: block;\r\n    list-style: none;\r\n    margin: 0;\r\n    padding: 10px 30px 24px 30px;\r\n}\r\n\r\n.listing-features li {\r\n        font-size: 15px;\r\n        color: #888;\r\n        display: inline-block;\r\n        margin-right: 28px;\r\n        padding: 14px 0 0 0;\r\n        line-height: 26px;\r\n    }\r\n\r\n.listing-features li:last-child {\r\n            margin: 0;\r\n        }\r\n\r\n.listing-features li span {\r\n            display: block;\r\n            color: #444;\r\n        }\r\n\r\n/* Listing - List Style\r\n