eft: -1.5%;\r\n    width: 103%;\r\n    position: relative;\r\n}\r\n\r\n.grid-layout .listing-item {\r\n        display: inline-block;\r\n        width: 47%;\r\n        float: left;\r\n        margin-left: 1.5%;\r\n        margin-right: 1.5%;\r\n        position: relative;\r\n    }\r\n\r\n/* Three Columns Grid Layout*/\r\n\r\n.grid-layout-three {\r\n    left: -1%;\r\n    width: 102%;\r\n    position: relative;\r\n}\r\n\r\n.grid-layout-three .listing-item {\r\n        display: inline-block;\r\n        width: 31.33%;\r\n        float: left;\r\n        margin-left: 1%;\r\n        margin-right: 1%;\r\n        position: relative;\r\n    }\r\n\r\n/* Layout Switcher */\r\n\r\n.layout-switcher.hidden {\r\n    display: none;\r\n}\r\n\r\n.layout-switcher a {\r\n    width: 36px;\r\n    height: 36px;\r\n    background-color: #f0f0f0;\r\n    display: inline-block;\r\n    border-radius: 3px;\r\n    color: #a0a0a0;\r\n    font-size: 20px;\r\n    line-height: 38px;\r\n    text-align: center;\r\n    -webkit-transition: all 0.3s;\r\n    transition: all 0.3s;\r\n}\r\n\r\n.layout-switcher a.active,\r\n    .layout-switcher a:hover {\r\n        color: #fff;\r\n        background-color: #66676b;\r\n    }\r\n\r\n.layout-switcher {\r\n    text-align: right;\r\n}\r\n\r\n/* Details Button */\r\n\r\n.details.button.border {\r\n    padding: 8px 16px;\r\n    position: absolute;\r\n    top: 50%;\r\n    right: 35px;\r\n    margin: 0;\r\n    -webkit-transform: translate(0,-51%) scale(1);\r\n            transform: translate(0,-51%) scale(1);\r\n    -webkit-transition: all 0.25s;\r\n    transition: all 0.25s;\r\n    overflow: hidden;\r\n    display: none;\r\n}\r\n\r\n.list-layout .details.button.border {\r\n    display: block;\r\n}\r\n\r\n.details.button.border:hover {\r\n    padding-right: 32px;\r\n}\r\n\r\n.details.button.border:after {\r\n    content: \"\\f105\";\r\n    font-family: \"FontAwesome\";\r\n    font-size: 18px;\r\n    position: absolute;\r\n    opacity: 0;\r\n    color: #fff;\r\n    -webkit-transition: all 0.25s;\r\n    transition: all 0.25s;\r\n    right: 5px;\r\n}\r\n\r\n.details.button.border:hover:after {\r\n    opacity: 1;\r\n    right: 15px;\r\n}\r\n\r\n/* Listing - Compact Grid\r\n------------------------------------- */\r\n\r\n.listing-item.compact {\r\n    overflow: hidden;\r\n    position: relative;\r\n    z-index: 99;\r\n}\r\n\r\n.compact .listing-img-content {\r\n    padding: 0;\r\n}\r\n\r\n.listing-hidden-content {\r\n    position: relative;\r\n    width: 100%;\r\n    background-color: #66676b;\r\n    color: #fff;\r\n    height: 0;\r\n    -webkit-transition: all 0.4s;\r\n    transition: all 0.4s;\r\n    padding: 0;\r\n    margin: 0;\r\n    list-style: none;\r\n    font-size: 14px;\r\n    padding: 0 0 0 25px;\r\n    display: block;\r\n    overflow: hidden;\r\n}\r\n\r\n.listing-item.compact:hover .listing-hidden-content {\r\n    height: 70px;\r\n}\r\n\r\n.listing-item.compact li {\r\n    display: inline-block;\r\n    color: rgba(255,255,255,0.8);\r\n    line-height: 20px;\r\n    padding: 15px 20px 0 0;\r\n    position: relative;\r\n    -webkit-transition: all 0.5s;\r\n    transition: all 0.5s;\r\n    opacity: 0;\r\n}\r\n\r\n.listing-item.compact li:last-child {\r\n        padding: 0;\r\n    }\r\n\r\n.listing-item.compact:hover li {\r\n    opacity: 1;\r\n}\r\n\r\n.listing-item.compact li span {\r\n    display: block;\r\n    font-size: 14px;\r\n    color: #fff;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n/* Overlay */\r\n\r\n.compact .listing-img-container:before {\r\n    opacity: 0.9;\r\n    background: -webkit-gradient(linear, left top, left bottom, color-stop(55%, transparent), to(#2f2f2f));\r\n    background: linear-gradient(to bottom, transparent 55%, #2f2f2f);\r\n    -webkit-transition: opacity .5s;\r\n    transition: opacity .5s;\r\n}\r\n\r\n.compact .listing-img-container:after {\r\n    opacity: 0;\r\n    background: -webkit-gradient(linear, left top, left bottom, color-stop(35%, transparent), color-stop(95%, #2f2f2f));\r\n  