ont-weight: 500;\r\n    color: #333;\r\n}\r\n\r\n.comment-by h5 {\r\n        margin: 0 0 7px 0;\r\n    }\r\n\r\n.comment-by a.reply,\r\n    .comment-by a.reply i {\r\n        -webkit-transition: all 0.2s ease-in-out;\r\n        transition: all 0.2s ease-in-out;\r\n    }\r\n\r\n.comment-by a.reply {\r\n        display: inline;\r\n        background-color: #f0f0f0;\r\n        padding: 5px 16px;\r\n        line-height: 22px;\r\n        font-size: 13px;\r\n        color: #333;\r\n        font-weight: 500;\r\n        position: absolute;\r\n        right: 0;\r\n        top: 15px;\r\n        border-radius: 50px;\r\n    }\r\n\r\n.comment-by a.reply i {\r\n            font-size: 12px;\r\n            margin-right: 1px;\r\n        }\r\n\r\n.comment-by a.reply:hover {\r\n            background-color: #66676b;\r\n        }\r\n\r\n.comment-by a.reply:hover,\r\n            .comment-by a.reply:hover i {\r\n                color: #fff;\r\n            }\r\n\r\n.comment-by span.date {\r\n        color: #888;\r\n        font-weight: 500;\r\n        margin: 0px 0 0 0;\r\n        float: none;\r\n        display: block;\r\n        text-align: left;\r\n    }\r\n\r\n/* Add Comment */\r\n\r\n.add-comment label span {\r\n    color: #e24747;\r\n}\r\n\r\n.add-comment input {\r\n    margin: 0 0 25px 0;\r\n    width: 40%;\r\n    min-width: 40%;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.add-comment textarea:focus,\r\n    .add-comment input:focus {\r\n        -webkit-transition: box-shadow 0.2s !important;\r\n        transition: box-shadow 0.2s !important;\r\n        box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.07);\r\n    }\r\n\r\n.add-comment textarea {\r\n    margin: 0 0 30px 0;\r\n    width: 100%;\r\n    min-width: 100%;\r\n    min-height: 220px;\r\n    resize: vertical;\r\n    -webkit-transition: none;\r\n    transition: none;\r\n    width: 100%;\r\n    box-sizing: border-box;\r\n}\r\n\r\n/* About Author\r\n------------------------------------- */\r\n\r\n.about-author {\r\n    margin: 10px 0 45px 0;\r\n    padding: 30px 35px;\r\n    background: #f8f8f8;\r\n    border-radius: 3px;\r\n}\r\n\r\n.about-author p {\r\n        margin: 5px 0 5px 0;\r\n    }\r\n\r\n.about-author a {\r\n        position: relative;\r\n        top: -7px;\r\n    }\r\n\r\n.about-author h4 {\r\n        letter-spacing: 0;\r\n    }\r\n\r\n.about-author img {\r\n        float: left;\r\n        margin: 7px 5px 5px 5px;\r\n        width: 90px;\r\n        height: 90px;\r\n        border-radius: 50%;\r\n    }\r\n\r\n.about-description {\r\n    margin: 2px 0 0 130px;\r\n}\r\n\r\n/* Share Buttons\r\n------------------------------------- */\r\n\r\n.share-buttons {\r\n    padding: 0;\r\n    list-style: none;\r\n}\r\n\r\n.share-buttons li {\r\n        display: inline-block;\r\n        margin: 0 6px 8px 0;\r\n    }\r\n\r\n.share-buttons li a {\r\n            background: #fff;\r\n            border: 1px solid #666;\r\n            border-radius: 3px;\r\n            font-weight: 500;\r\n            font-size: 14px;\r\n            padding: 8px 20px;\r\n            -webkit-transition: 0.3s;\r\n            transition: 0.3s;\r\n            display: inline-block;\r\n            line-height: 17px;\r\n        }\r\n\r\n.share-buttons li a:hover {\r\n                color: #fff;\r\n            }\r\n\r\n.share-buttons li i {\r\n            font-size: 16px;\r\n            position: relative;\r\n            right: 3px;\r\n            top: 1px;\r\n        }\r\n\r\n/* fb */\r\n\r\n.share-buttons li a.fb-share:hover {\r\n            background: #3b5998;\r\n            color: #fff;\r\n        }\r\n\r\n.share-buttons li a.fb-share {\r\n            border-color: #3b5998;\r\n            color: #3b5998;\r\n        }\r\n\r\n/* twitter */\r\n\r\n.share-buttons li a.twitter-share:hover {\r\n            background: #1da1f2;\r\n            color: #fff;\r\n        }\r\n\r\n.share-buttons li a.twitter-share {\r\n            border-color: #1da1f2;\r\n            color: #1da1f2;\r\n        }\r\n\r\n/* gplus */\r\n\r\n.share-buttons li a.gplus-share:hover {\r\n            background: #dd4b39;\r\n            color: #fff;\r\n        }\r\n\r\n.share-buttons li a.gplus-share {\r\n            border-color: #dd4b39;\r\n            color: #dd4b39;\r\n        }\r\n\r\n/* pinterest */\r\n\r\n.share-buttons li a.pinterest-share:hover {\r\n            background: #bd081c;\r\n            color: #fff;\r\n        }\r\n\r\n.share-buttons li a.pinterest-share {\r\n            border-color: #bd081c;\r\n            color: #bd081c;\r\n        }\r\n\r\n/* Post Navigation\r\n------------------------------------- */\r\n\r\n#posts-nav {\r\n    padding: 0;\r\n    list-style: none;\r\n    width: 100%;\r\n    position: relative;\r\n    margin: 0;\r\n    display: inline-block;\r\n    min-height: 60px;\r\n}\r\n\r\n#posts-nav li span {\r\n        display: block;\r\n        color: #999;\r\n        font-size: 15px;\r\n        margin-bottom: 1px;\r\n        font-weight: 500;\r\n    }\r\n\r\n#posts-nav li {\r\n        position: absolute;\r\n        font-size: 17px;\r\n        font-weight: 500;\r\n        width: 50%;\r\n    }\r\n\r\n#posts-nav li a {\r\n            -webkit-transition: color 0.3s 0s, -webkit-transform 0.3s;\r\n            transition: color 0.3s 0s, -webkit-transform 0.3s;\r\n            transition: transform 0.3s, color 0.3s 0s;\r\n            transition: transform 0.3s, color 0.3s 0s, -webkit-transform 0.3s;\r\n            color: #333;\r\n            display: inline-block;\r\n        }\r\n\r\n#posts-nav li a:hover {\r\n                color: #66676b;\r\n            }\r\n\r\n#posts-nav li.next-post {\r\n            right: 0;\r\n            text-align: right;\r\n        }\r\n\r\n#posts-nav li.prev-post {\r\n            left: 0;\r\n        }\r\n\r\n#posts-nav li.next-post a {\r\n            padding-right: 50px;\r\n        }\r\n\r\n#posts-nav li.prev-post a {\r\n            padding-left: 50px;\r\n        }\r\n\r\n#posts-nav li.prev-post a:before,\r\n            #posts-nav li.next-post a:before {\r\n                font-family: \"simple-line-icons\";\r\n                font-size: 20px;\r\n                position: absolute;\r\n                color: inherit;\r\n                top: 50%;\r\n                -webkit-transform: translate3d(0,-50%,0);\r\n                        transform: translate3d(0,-50%,0);\r\n                -webkit-transition: -webkit-transform 0.3s;\r\n                transition: -webkit-transform 0.3s;\r\n                transition: transform 0.3s;\r\n                transition: transform 0.3s, -webkit-transform 0.3s;\r\n            }\r\n\r\n#posts-nav li.next-post a:before {\r\n            right: 0;\r\n            content: \"\\e606\";\r\n        }\r\n\r\n#posts-nav li.prev-post a:before {\r\n            left: 0;\r\n            content: \"\\e605\"\r\n        }\r\n\r\n#posts-nav li.next-post a:hover:before {\r\n            -webkit-transform: translate3d(5px,-50%,0);\r\n                    transform: translate3d(5px,-50%,0);\r\n        }\r\n\r\n#posts-nav li.prev-post a:hover:before {\r\n            -webkit-transform: translate3d(-5px,-50%,0);\r\n                    transform: translate3d(-5px,-50%,0);\r\n        }\r\n\r\n/* ------------------------------------------------------------------- */\r\n\r\n/* 03. Scripts\r\n---------------------------------------------------------------------- */\r\n\r\n/* ---------------------------------- */\r\n\r\n/* Google Maps\r\n------------------------------------- */\r\n\r\n.infoBox {\r\n    border-radius: 3px;\r\n}\r\n\r\n.map-box {\r\n    background-color: #fff;\r\n    margin-bottom: 20px;\r\n    border-radius: 3px;\r\n    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.12);\r\n    position: relative;\r\n    left: -62px;\r\n}\r\n\r\n.map-box h4 {\r\n        margin: 0;\r\n        padding: 0;\r\n    }\r\n\r\n.map-box h4 a {\r\n            padding: 0px;\r\n            font-size: 17px;\r\n            line-height: 25px;\r\n            -webkit-transition: all 0.2s ease-in-out;\r\n            transition: all 0.2s ease-in-out;\r\n            display: inline-block;\r\n        }\r\n\r\n.map-box h4 a:hover {\r\n                color: #66676b;\r\n            }\r\n\r\n.map-box p {\r\n        padding: 0;\r\n        line-height: 25px;\r\n        margin: 2px 0 0 0;\r\n        font-size: 14px;\r\n    }\r\n\r\n.map-box ul.listing-details {\r\n        padding: 0;\r\n        font-size: 14px;\r\n        display: none;\r\n    }\r\n\r\n.map-box .listing-title {\r\n        padding: 22px;\r\n        padding-top: 20px;\r\n    }\r\n\r\n.map-box .listing-img-content {\r\n        padding: 18px 22px;\r\n    }\r\n\r\n.map-box .listing-img-content span {\r\n            font-size: 21px;\r\n        }\r\n\r\n.map-box .listing-img-content .listing-price i {\r\n            font-size: 15px;\r\n            margin: 0;\r\n        }\r\n\r\n.infoBox > img {\r\n    position: absolute !important;\r\n    right: 0px;\r\n    top: 0px;\r\n}\r\n\r\n.map-box .listing-img-container:before {\r\n    background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, transparent), to(#2f2f2f));\r\n    background: linear-gradient(to bottom, transparent 50%, #2f2f2f);\r\n}\r\n\r\n.map-box .listing-img-container:after {\r\n    background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, transparent), to(#66676b));\r\n    background: linear-gradient(to bottom, transparent 50%, #66676b);\r\n}\r\n\r\n/* Cluster styles */\r\n\r\n.cluster img {\r\n    display: none\r\n}\r\n\r\n.cluster {\r\n    width: 36px !important;\r\n    height: 36px !important;\r\n}\r\n\r\n.cluster div {\r\n        text-align: center;\r\n        font-size: 15px !important;\r\n        color: #fff !important;\r\n        font-weight: 500;\r\n        border-radius: 50%;\r\n        width: 36px !important;\r\n        height: 36px !important;\r\n        line-height: 36px !important;\r\n        background-color: #66676b;\r\n    }\r\n\r\n.cluster div:before {\r\n            border: 7px solid #66676b;\r\n            opacity: 0.2;\r\n            box-shadow: inset 0 0 0 4px #66676b;\r\n            content: '';\r\n            position: absolute;\r\n            border-radius: 50%;\r\n            top: 0;\r\n            left: 0;\r\n            width: 100%;\r\n            height: 100%;\r\n            -webkit-animation: cluster-animation 2.5s infinite;\r\n                    animation: cluster-animation 2.5s infinite;\r\n        }\r\n\r\n@-webkit-keyframes cluster-animation {\r\n    0%,100% {\r\n        -webkit-transform: scale(1.3) rotate(0deg);\r\n                transform: scale(1.3) rotate(0deg)\r\n    }\r\n\r\n    50% {\r\n        -webkit-transform: scale(1.5) rotate(90deg);\r\n                transform: scale(1.5) rotate(90deg)\r\n    }\r\n}\r\n\r\n@keyframes cluster-animation {\r\n    0%,100% {\r\n        -webkit-transform: scale(1.3) rotate(0deg);\r\n                transform: scale(1.3) rotate(0deg)\r\n    }\r\n\r\n    50% {\r\n        -webkit-transform: scale(1.5) rotate(90deg);\r\n                transform: scale(1.5) rotate(90deg)\r\n    }\r\n}\r\n\r\n/* Close Button */\r\n\r\n.infoBox-close {\r\n    position: absolute;\r\n    top: 10px;\r\n    right: 10px;\r\n    display: inline-block;\r\n    z-index: 999;\r\n    text-align: center;\r\n    line-height: 31px;\r\n    cursor: pointer;\r\n    font-size: 15px;\r\n    font-weight: 500;\r\n    height: 31px;\r\n    width: 31px;\r\n    background-color: #fff;\r\n    color: #333;\r\n    -webkit-transition: all 0.2s ease-in-out;\r\n    transition: all 0.2s ease-in-out;\r\n    font-family: \"FontAwesome\";\r\n    border-radius: 3px;\r\n    -webkit-text-stroke: 1px #fff;\r\n}\r\n\r\n.infoBox-close:hover {\r\n        color: #fff;\r\n        background-color: #66676b;\r\n        -webkit-text-stroke: 1px #66676b;\r\n    }\r\n\r\n/* Read More Icon */\r\n\r\n.map-box-icon {\r\n    height: 38px;\r\n    width: 38px;\r\n    position: absolute;\r\n    bottom: 0;\r\n    right: 0;\r\n    background-color: #3685cf;\r\n    display: block;\r\n    color: #fff;\r\n    font-size: 12px;\r\n    z-ind