rder-color: #66676b;\r\n            }\r\n\r\n.tab-reviews {\r\n    color: #aaa;\r\n}\r\n\r\n.tabs-nav li.active .tab-reviews {\r\n    color: #fff;\r\n}\r\n\r\n.tabs-nav li a:hover,\r\n.tabs-nav li.active a {\r\n    border-color: #66676b;\r\n    color: #66676b;\r\n    z-index: 11;\r\n}\r\n\r\n.tab-content {\r\n    padding: 28px 33px;\r\n    position: relative;\r\n    z-index: 10;\r\n    display: inline-block;\r\n    width: 100%;\r\n}\r\n\r\n.tabs-container {\r\n    overflow: hidden;\r\n    width: 100%;\r\n}\r\n\r\n.tab-content p:last-child {\r\n    margin-bottom: 0;\r\n}\r\n\r\n.style-2 .tabs-container {\r\n    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);\r\n    border-radius: 3px;\r\n}\r\n\r\n/* Tabs Style 1 */\r\n\r\n.style-1 .tabs-container .tab-content {\r\n    border: none;\r\n    border-top: 1px solid #e0e0e0;\r\n    padding: 30px 0 0 0;\r\n}\r\n\r\n/* Taba Style 3 */\r\n\r\n.style-3 .tabs-nav {\r\n    text-align: center;\r\n}\r\n\r\n.style-3 .tabs-nav li {\r\n        float: none;\r\n        line-height: 32px;\r\n        padding: 0;\r\n        margin-right: 4px;\r\n        position: relative;\r\n        z-index: 12;\r\n        display: inline-block;\r\n        text-align: center;\r\n    }\r\n\r\n.style-3 .tabs-nav li a {\r\n            border: none;\r\n            color: #333;\r\n            display: inline-block;\r\n            outline: none;\r\n            padding: 7px 24px;\r\n            text-decoration: none;\r\n            font-size: 14px;\r\n            z-index: 11;\r\n            position: relative;\r\n            margin-bottom: -1px;\r\n            background-color: #fff;\r\n            border-radius: 3px;\r\n            font-size: 13px;\r\n        }\r\n\r\n.style-3 .tabs-nav li a:hover,\r\n            .style-3 .tabs-nav li.active a {\r\n                border-color: #66676b;\r\n                background-color: #66676b;\r\n                color: #fff;\r\n                z-index: 11;\r\n            }\r\n\r\n.style-3 .tabs-container .tab-content {\r\n 